* {
  margin: 0;
  padding: 0;
}

html {
  --hover-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo__link {
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.logo__image {
  width: 100%;
  height: 100%;
}

.nav {
  margin-right: 50px;
}

.nav__list {
  list-style: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__link:hover {
  color: var(--hover-color);
}

.is-active {
  color: var(--hover-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--hover-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

